#contact {
  background: #191919;
  padding-top: 56px;
  padding-bottom: 102px;
  color: #636363;
}

#contact a,
#contact a:visited {
  color: #c9c9c9;
  text-decoration: none;
}
#contact a:hover,
#contact a:focus {
  color: rgb(126, 109, 100);
}
.lead {
  color: rgb(197, 169, 153);
}
#contact h4 {
  display: block;
  margin: 18px auto;
  width: 220px;
  font-size: 18px;
  color: #636363;
  margin-bottom: 6px;
  border-bottom: 0.2px solid rgb(131, 124, 124);
  text-align: left;
}
#contact h4 span {
  color: #c9c9c9;
}
#contact p.lead {
  font-size: 24px;
}
#contact .header-col {
  padding-top: 6px;
}
.contact-info {
  letter-spacing: 1px;
}
